.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: white;
    border-bottom: 1px solid #e0e0e0;
  }
  
  .logo {
    display: flex;
    align-items: center;
    font-size: 24px;
    font-weight: bold;
    color: #222;
    margin-right: 30px;
  }
  
  .nav {
    flex: 2; /* 메뉴가 중앙에 배치되도록 비율 조정 */
    display: flex;
    justify-content: flex-start; /* 좌측 정렬 */
  }
  
  .nav ul {
    list-style: none;
    display: flex;
    gap: 20px;
    padding: 0;
    margin: 0;
  }
  
  .nav li {
    font-size: 16px;
    cursor: pointer;
    color: #333;
  }
  
  .divider {
    color: #ccc;
  }
  
  .bold {
    font-weight: bold;
  }
  
  .header-actions {
    flex: 1; /* 우측에 고정 */
    display: flex;
    justify-content: flex-end; /* 우측 정렬 */
    align-items: center;
    gap: 16px;
  }
  
  .search-button,
  .help-button {
    font-size: 18px;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
  
  .auth-links {
    display: flex;
    gap: 16px; /* 로그인과 회원가입 버튼 간격 */
  }
  
  .auth-links a {
    padding: 10px 20px;
    border-radius: 8px;
    font-size: 16px;
    text-decoration: none;
    text-align: center;
  }
  
  .auth-links a:first-child {
    background-color: #fff;
    color: #007bff;
    border: 1px solid #007bff;
    transition: all 0.3s ease;
  }
  
  .auth-links a:first-child:hover {
    background-color: #007bff;
    color: white;
  }
  
  .auth-links a:last-child {
    background-color: #007bff;
    color: white;
    border: none;
    transition: all 0.3s ease;
  }
  
  .auth-links a:last-child:hover {
    background-color: #0056b3;
  }
  
  .profile-menu {
    position: relative;
  }
  
  .profile-icon img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
    border: 2px solid transparent;
    transition: border 0.3s ease;
  }
  
  .profile-icon img:hover {
    border-color: #007bff;
  }
  
  .dropdown-menu {
    position: absolute;
    top: calc(100% + 8px);
    right: 0;
    background-color: white;
    border-radius: 12px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
    padding: 10px 0;
    z-index: 10;
    min-width: 180px; /* 최소 너비 설정 */
    overflow: hidden; /* 요소가 넘치지 않게 */
  }
  
  .dropdown-menu a,
  .logout-button {
    display: block;
    padding: 12px 20px;
    text-decoration: none;
    color: #333;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .dropdown-menu a:hover,
  .logout-button:hover {
    background-color: #f0f4ff;
    color: #007bff;
  }
  
  .logout-button {
    background: none;
    border: none;
    width: 100%;
    text-align: left;
  }
  
  .dropdown-divider {
    height: 1px;
    margin: 8px 0;
    background-color: #e0e0e0;
  }
  
  .logo-link {
    text-decoration: none; /* 밑줄 제거 */
    color: inherit; /* 부모 요소의 색상 유지 */
    display: flex; /* 텍스트와 베타 표시를 가로로 정렬 */
    align-items: center;
  }
  
  .logo-text {
    font-size: 24px;
    font-weight: bold;
    margin-right: 5px;
  }
  
  .logo-link:hover {
    opacity: 0.8; /* 클릭 시 약간 흐리게 */
  }
  
  .profile-icon img {
    border-radius: 50%;
    cursor: pointer;
    border: 2px solid transparent;
  }
  
  